.bod {
  background-color: cadetblue;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 60vh;
  font-family: Arial, sans-serif;
}

.Main .Login {
  background-color: aliceblue;
  padding: 20px;
  border-radius: 8px;
  display: flex;
  flex-direction: column;
  gap: 15px;
}
input {
  padding: 10px;
  margin: 3px 0;
  border-radius: 5px;
  border: 1px solid #ccc;
}

button {
  padding: 10px;
  border: none;
  border-radius: 5px;
  background-color: darkblue;
  color: white;
  cursor: pointer;
}

button:hover {
  background-color: blue;
}
