.Main {
  background-color: cadetblue;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 50vh;
  font-family: Arial, sans-serif;
}

.Main .Login {
  background-color: aliceblue;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 4px 8px rbga(0, 0, 0, 0.2);
  display: flex;
  flex-direction: column;
  gap: 10px;
}
input {
  padding: 10px;
  margin: 3px 0;
  border-radius: 5px;
  border: 1px solid #ccc;
}

button {
  padding: 10px;
  border: none;
  border-radius: 5px;
  background-color: #007bff;
  color: white;
  cursor: pointer;
}

button:hover {
  background-color: #0056b3;
}
